:root {
  --bgApp: linear-gradient(62deg, #8ec5fc 0%, #e0c3fc 100%);
  --bgCont: #c8c6d7;
  --bgTileActive: #ce6a85;
  --bgTilePassive: #4a4063;
  --bgTextarea: #ce6a856e;
  --bgTextareaFocus: #ce6a853d;
  --textLight: #c8c6d7;
  --textDark: #160f29;

  --bgAppDark: radial-gradient(
    circle farthest-corner at 83.7% 4.3%,
    rgb(69, 0, 68) 0%,
    rgb(5, 18, 32) 90%
  );
  --bgContDark: rgb(5, 18, 32);
  --bgTileActiveDark: rgb(69, 0, 68);
  --bgTilePassiveDark: #292337;
  --bgTextareaDark: rgb(69, 0, 68);
  --bgTextareaFocusDark: rgba(69, 0, 68, 0.522);

  --durationColor: 400ms;
  --durationOther: 100ms;
  --durationAnimate: 400ms;
}
* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  -webkit-transition: background-color linear var(--durationColor),
    color linear var(--durationColor),
    border-radius ease-in-out var(--durationOther),
    -webkit-transform ease-in-out var(--durationOther);
  transition: background-color linear var(--durationColor),
    color linear var(--durationColor),
    border-radius ease-in-out var(--durationOther),
    -webkit-transform ease-in-out var(--durationOther);
  -o-transition: background-color linear var(--durationColor),
    color linear var(--durationColor),
    transform ease-in-out var(--durationOther),
    border-radius ease-in-out var(--durationOther);
  transition: background-color linear var(--durationColor),
    color linear var(--durationColor),
    transform ease-in-out var(--durationOther),
    border-radius ease-in-out var(--durationOther);
  transition: background-color linear var(--durationColor),
    color linear var(--durationColor),
    transform ease-in-out var(--durationOther),
    border-radius ease-in-out var(--durationOther),
    -webkit-transform ease-in-out var(--durationOther);
  /* transition-property: background-color, color, border-radius, transform; */
  -webkit-tap-highlight-color: transparent;
}

.todo-app {
  padding: 2rem;
  background: var(--bgApp);
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  min-width: 100vw;
  min-height: 100vh;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  gap: 50px;
  /* transition: color, background-color, filter linear 500ms; */
  color: var(--textDark);
}
.todo-app-header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  gap: 40px;
  padding: 20px;
  width: 100%;
}
.todo-app-header h1 {
  font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
  letter-spacing: 3px;
}
.btn {
  padding: 10px;
  font-size: 20px;
  display: inline;
  cursor: pointer;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  border-radius: 10px;
  /* transition: border-radius, background-color, ease-in-out 100ms; */
}
.btn:hover {
  background-color: rgba(0, 0, 0, 0.155);
  border-radius: 20px;
  -webkit-transform: scale(0.9);
  -ms-transform: scale(0.9);
  transform: scale(0.9);
}
.todo-tile input[type="checkbox"],
.theme-toggle-btn input[type="checkbox"] {
  display: none;
}

.toggle-label {
  cursor: pointer;
  width: 60px;
  height: 26px;
  background-color: var(--textDark);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  border-radius: 50px;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  padding: 5px;
  position: relative;
  -webkit-transform: scale(1.5);
  -ms-transform: scale(1.5);
  transform: scale(1.5);
  /* transition: transform ease-in-out 400ms; */
}
.toggle-label:hover {
  -webkit-transform: scale(1.4);
  -ms-transform: scale(1.4);
  transform: scale(1.4);
}

.toggle-ball {
  width: 22px;
  height: 22px;
  background-color: var(--textLight);
  position: absolute;
  top: 2px;
  left: 4px;
  border-radius: 50%;
  /* transition: transform 0.2s linear; */
}

.toggle-moon {
  color: var(--textLight);
}

.toggle-sun {
  color: rgba(255, 255, 0, 0.632);
}

/*  target the elemenent after the label*/
.toggle-checkbox:checked + .toggle-label .toggle-ball {
  -webkit-transform: translateX(30px);
  -ms-transform: translateX(30px);
  transform: translateX(30px);
}

.todo-cont {
  padding: 2rem;
  background-color: var(--bgCont);
  -webkit-box-shadow: rgba(14, 30, 37, 0.12) 0px 2px 4px 0px,
    rgba(14, 30, 37, 0.32) 0px 2px 16px 0px;
  box-shadow: rgba(14, 30, 37, 0.12) 0px 2px 4px 0px,
    rgba(14, 30, 37, 0.32) 0px 2px 16px 0px;
  border-radius: 10px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  min-width: 60%;
  color: var(--textDark);
}

.todo-list-cont {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  gap: 30px;
  -webkit-animation: fade var(--durationAnimate) ease-in-out;
  animation: fade var(--durationAnimate) ease-in-out;
}
.todo-list-cont h2 {
  font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
  letter-spacing: 2px;
}

.todo-list-tiles {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -ms-flex-direction: row;
  flex-direction: row;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  gap: 30px;
}

.todo-tile {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: distribute;
  justify-content: space-around;
  gap: 15px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  max-width: 50%;
  background-color: var(--bgTileActive);
  -webkit-box-shadow: 0px 3px 8px rgba(0, 0, 0, 0.24);
  box-shadow: 0px 3px 8px rgba(0, 0, 0, 0.24);
  border-radius: 10px;
  padding: 10px;
  /* transition: all ease-in-out 200ms; */
  /* transition-property: color, background-color, filter, transform; */
  -webkit-animation: fadeDown var(--durationAnimate) ease-in-out;
  animation: fadeDown var(--durationAnimate) ease-in-out;
}
.tile-passive {
  background-color: var(--bgTilePassive);
}

.add-task {
  font-size: 30px;
  min-width: 120px;
  min-height: 140px;

  cursor: pointer;
}
.todo-tile:hover {
  -webkit-filter: hue-rotate(0deg);
  filter: hue-rotate(0deg);
  -webkit-transform: scale(1.08);
  -ms-transform: scale(1.08);
  transform: scale(1.08);
}
.todo-tile-top {
  height: 100%;
  width: 100%;
  color: var(--textLight);
  padding: 20px;
  background-color: rgba(0, 0, 0, 0.277);
  border-radius: 20px;
  overflow-y: auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  text-align: center;
  gap: 10px;
  cursor: pointer;
  /* transition: filter, transform ease-in-out 200ms; */
  -webkit-animation: fadeUp var(--durationAnimate) ease-in-out 50ms;
  animation: fadeUp var(--durationAnimate) ease-in-out 50ms;
}
.todo-tile-top h3 {
  font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
  font-weight: 200;
}
.todo-tile-bottom {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -ms-flex-direction: row;
  flex-direction: row;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-animation: fadeDown var(--durationAnimate) ease-in-out 50ms;
  animation: fadeDown var(--durationAnimate) ease-in-out 50ms;
}
.todo-edit-cont {
  min-width: 60vw;
  min-height: 60vh;
  display: -ms-grid;
  display: grid;
  row-gap: 20px;
  -webkit-column-gap: 30px;
  -moz-column-gap: 30px;
  column-gap: 30px;
  -ms-grid-columns: 80px 30px auto 30px 80px;
  grid-template-columns: 80px auto 80px;
  -ms-grid-rows: 60px 20px auto;
  grid-template-rows: 60px auto;
  grid-template-areas:
    "home title save"
    "content  content content";
  -webkit-animation: fade var(--durationAnimate) ease-in-out;
  animation: fade var(--durationAnimate) ease-in-out;
}

textarea {
  padding: 10px;
  background-color: var(--bgTextarea);
  color: var(--textDark);
  border-radius: 10px;
  width: 250px;
  font-size: 20px;
  border-radius: 6px;
  border: 0;
  resize: none;
  width: 100%;
  /* transition: background-color ease-in-out 200ms; */
  -webkit-box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
}
textarea:focus {
  outline: none;
  border-radius: 15px;
  background-color: var(--bgTextareaFocus);
}

.task-title {
  -ms-grid-row: 1;
  -ms-grid-column: 3;
  grid-area: title;
  /* height: 50px; */
  width: 100%;
  min-width: 100px;
  text-align: center;
  font-size: 22px;
  font-weight: 400;
  -webkit-animation: fadeUp var(--durationAnimate) ease-in-out 50ms;
  animation: fadeUp var(--durationAnimate) ease-in-out 50ms;
  font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
}
.task-content {
  -ms-grid-row: 3;
  -ms-grid-column: 1;
  -ms-grid-column-span: 5;
  grid-area: content;
  -webkit-animation: fadeDown var(--durationAnimate) ease-in-out 50ms;
  animation: fadeDown var(--durationAnimate) ease-in-out 50ms;
}
.save-btn {
  -ms-grid-row: 1;
  -ms-grid-column: 5;
  grid-area: save;
  -webkit-animation: fadeRight var(--durationAnimate) ease-in-out 50ms;
  animation: fadeRight var(--durationAnimate) ease-in-out 50ms;
}
.home-btn {
  -ms-grid-row: 1;
  -ms-grid-column: 1;
  grid-area: home;
  -webkit-animation: fadeLeft var(--durationAnimate) ease-in-out 50ms;
  animation: fadeLeft var(--durationAnimate) ease-in-out 50ms;
}

@-webkit-keyframes fade {
  0% {
    -webkit-transform: scale(1.2);
    transform: scale(1.2);
    opacity: 0;
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
    opacity: 1;
  }
}

@keyframes fade {
  0% {
    -webkit-transform: scale(1.2);
    transform: scale(1.2);
    opacity: 0;
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
    opacity: 1;
  }
}
@-webkit-keyframes fadeUp {
  0% {
    -webkit-transform: translateY(5vh);
    transform: translateY(5vh);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
    opacity: 1;
  }
}
@keyframes fadeUp {
  0% {
    -webkit-transform: translateY(5vh);
    transform: translateY(5vh);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
    opacity: 1;
  }
}
@-webkit-keyframes fadeDown {
  0% {
    -webkit-transform: translateY(-5vh);
    transform: translateY(-5vh);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
    opacity: 1;
  }
}
@keyframes fadeDown {
  0% {
    -webkit-transform: translateY(-5vh);
    transform: translateY(-5vh);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
    opacity: 1;
  }
}

@-webkit-keyframes fadeLeft {
  0% {
    -webkit-transform: translateX(5vw);
    transform: translateX(5vw);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes fadeLeft {
  0% {
    -webkit-transform: translateX(5vw);
    transform: translateX(5vw);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
    opacity: 1;
  }
}
@-webkit-keyframes fadeRight {
  0% {
    -webkit-transform: translateX(-5vw);
    transform: translateX(-5vw);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
    opacity: 1;
  }
}
@keyframes fadeRight {
  0% {
    -webkit-transform: translateX(-5vw);
    transform: translateX(-5vw);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
    opacity: 1;
  }
}

@media screen and (max-width: 600px) {
  .todo-app {
    padding: 2rem 0;
  }
  .todo-cont {
    width: 100%;
  }
  .todo-app-header {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
  }
  .todo-edit-cont {
    -ms-grid-columns: auto auto auto auto;
    grid-template-columns: auto auto auto auto;
    -ms-grid-rows: 50px 80px auto;
    grid-template-rows: 50px 80px auto;
    grid-template-areas:
      ". home save ."
      "title title title title"
      "content content  content content";
  }
  .task-content {
    font-size: 15px;
  }
  .task-title {
    font-size: 20px;
  }
  .todo-cont {
    padding: 30px 20px;
  }
  .todo-tile-top {
    padding: 10px;
  }
  .todo-tile {
    max-width: 70%;
  }
  .todo-tile-top {
    font-size: 14px;
  }
  .task-title {
    -ms-grid-row: 2;
    -ms-grid-column: 1;
    -ms-grid-column-span: 4;
  }
  .task-content {
    -ms-grid-row: 3;
    -ms-grid-column: 1;
    -ms-grid-column-span: 4;
  }
  .save-btn {
    -ms-grid-row: 1;
    -ms-grid-column: 3;
  }
  .home-btn {
    -ms-grid-row: 1;
    -ms-grid-column: 2;
  }
}

@media screen and (max-width: 300px) {
  h1 {
    font-size: 28px;
  }
  .task-content {
    font-size: 12px;
  }
  .task-title {
    font-size: 15px;
  }
  .todo-tile {
    max-width: 100%;
  }
  .todo-tile-top {
    font-size: 12px;
  }
}

/* Dark Theme */

.dark-todo {
  background: var(--bgAppDark);
  color: var(--textLight);
}
.dark-todo .todo-cont {
  background-color: var(--bgContDark);
  color: var(--textLight);
}
.dark-todo .todo-tile {
  background-color: var(--bgTileActiveDark);
}
.dark-todo .tile-passive {
  background-color: var(--bgTilePassiveDark);
}
.dark-todo textarea {
  background-color: var(--bgTextareaDark);
  -webkit-box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  color: var(--textLight);
}
.dark-todo .btn {
  -webkit-box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
.dark-todo textarea:focus {
  background-color: var(--bgTextareaFocusDark);
}
